import baseCustomRulesAndChecks, {createRule} from '@github/axe-github'

const interactiveElements = ['a[href]', 'button', 'summary', 'select', 'input:not([type=hidden])', 'textarea']
const allowedTooltipRoles = ['button', 'link', 'gridcell']

const {checks: generatedChecks, rules: generatedRules} = baseCustomRulesAndChecks

createRule({
  id: 'element-with-interactive-behaviors',
  selector: 'div.js-details-target, span.js-details-target, a.js-details-target',
  help: 'This JS behavior requires a <button>',
  checks: [
    // Check the given element is a <button> element.
    (el: Element) => el.tagName === 'BUTTON',
  ],
})

createRule({
  id: 'custom-behavior-target-required-attr',
  selector: 'button.js-details-target',
  help: 'Toggle buttons must have aria-expanded attribute',
  checks: [
    /**
     * Check the given element has an `aria-expanded` attribute.
     *
     * @param {Element} el - The element.
     * @returns {boolean} - Will be `true` if the element has an `aria-expanded` attribute.
     */
    el => el.hasAttribute('aria-expanded'),
  ],
})

createRule({
  id: 'details-dialog-should-have-accessible-name',
  selector: 'details-dialog',
  help: 'Details Dialog must have an accessible name. Deprecation notice: Please use the new, accessible, Primer Dialog component.',
  helpUrl: 'https://primer.style/view-components/',
  any: ['aria-label', 'aria-labelledby'],
})

createRule({
  id: 'tooltip-only-contains-text',
  excludeHidden: false,
  selector: 'tool-tip',
  help: 'Tooltip should only contain text',
  checks: [
    /**
     * Check the tooltip only contains text.
     *
     * @param {Element} el - The tooltip element.
     * @returns {boolean} - Will be `true` if the element only contains text
     */
    el => {
      // innerHTML will encode special characters like `<` to `&lt;`
      return el.innerHTML.replaceAll('&lt;', '<').replaceAll('&gt;', '>') === el.textContent
    },
  ],
})

createRule({
  id: 'tooltip-is-adjacent-to-trigger',
  excludeHidden: false,
  selector: 'tool-tip',
  help: 'Tooltip should appear directly after the element it is attached to in the DOM. When setting a tooltip on a <summary> element, place it adjacent to <details>',
  checks: [
    /**
     * Checks the tooltip is adjacent after the element it is attached to.
     *
     * @param {Element} el - The tooltip element.
     * @returns {boolean} - Will be `true` if the tooltip appears directly after element it is applied to.
     */
    el => {
      const trigger = el.ownerDocument.getElementById(el.getAttribute('for') || '')
      if (!trigger) return false
      // When the tooltip is attached to a gridcell, we aren't able to place it directly adjacent so we make an exception.
      if (trigger.hasAttribute('role') && trigger.getAttribute('role') === 'gridcell') {
        return true
      }
      // When tooltip is attached to <summary>, it must be adjacent to <details> rather than <summary>.
      // Otherwise, the tooltip will only appear when <details> is expanded.
      if (trigger.tagName === 'SUMMARY') {
        return trigger.closest('details')?.nextElementSibling === el
      }
      return trigger.nextElementSibling === el
    },
  ],
})

createRule({
  id: 'tooltip-should-be-applied-to-interactive-elements',
  excludeHidden: false,
  selector: 'tool-tip',
  helpUrl: 'https://primer.style/design/accessibility/tooltip-alternatives',
  help: 'Tooltips should only be applied to interactive elements because it is not accessible on static elements',
  checks: [
    /**
     * Check the tooltip is applied to an interactive element.
     *
     * @param {Element} el - The tooltip element.
     * @returns {boolean} - Will be `true` if the tooltip is applied to an accepted, interactive element.
     */
    el => {
      const trigger = el.ownerDocument.getElementById(el.getAttribute('for') || '')
      if (!trigger) return true
      return (
        interactiveElements.filter(selector => trigger.matches(selector)).length > 0 ||
        (trigger.hasAttribute('role') && allowedTooltipRoles.includes(trigger.getAttribute('role') || ''))
      )
    },
  ],
})

createRule({
  id: 'action-menu-nested-elements-should-not-have-functionality',
  excludeHidden: false,
  selector: 'experimental-action-menu',
  help: 'Functionality should be added to the `menuitem` elements rather than the children. Semantics and functionality of child elements of `role="menuitem"` are ignored.',
  checks: [
    /**
     * Check the child elements of a `menuitem` do not contain functionality.
     *
     * @param {Element} el - The action-menu parent element.
     * @returns {boolean} - Will be `true` if the child elements of `menuitem` inside of <action-menu> do not contain functionality.
     */
    el => {
      const menuItems = el.querySelectorAll('[role="menuitem"],[role="menuitemcheckbox"],[role="menuitemradio"]')
      const invalidAttributes = ['href', 'onclick']
      let hasValidFunctionality = true

      // Check each child of each menu item
      for (const menuItem of menuItems) {
        const children = menuItem.querySelectorAll('*')
        if (children) {
          for (const child of children) {
            if (invalidAttributes.some(attr => child.hasAttribute(attr)) || child.tagName === 'CLIPBOARD-COPY') {
              hasValidFunctionality = false
            }
          }
        }
      }

      return hasValidFunctionality
    },
  ],
})

export default {
  rules: generatedRules,
  checks: generatedChecks,
}
